.discover-header{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: left;
  position: relative;
  width: 100%;
  background: rgb(21, 27, 36);
  padding: 32px;
  .header-box{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: 0;
    overflow: hidden;

    .back-style{
      position: absolute;
      left: 0px;
      top: 0px;
      width: 101%;
      height: 100%;
      mix-blend-mode: normal;
      z-index: 1;
      background: linear-gradient(rgba(40, 50, 62, 0.29) 0%, rgba(40, 52, 62, 0.55) 49.17%, rgba(40, 50, 62, 0.85) 100%);
    }
  }

  .header-info{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: left;
    flex: 1 1 auto !important;

    .breadcrumb {
      background-color: transparent;

      li {
        padding: 5px;
        color: white;

        svg {
          font-size: 12px;
        }

        &:last-child svg {
          display: none;
        }
      }
    }

  }
}

.Ideas-box{
  position: relative;
  z-index: 0;
  background-color: rgb(21, 27, 36);
  overflow: hidden;
  margin-bottom: 30px;
  .ideas-box-img{
    height: 188px;
    padding: 0px;
    img{
      object-fit: cover;
      width: 100%;
      height: 100%;
      filter: blur(0rem);
      opacity: 1;
      transition: all 0.4s ease-out 0s;
    }
  }

  .ideas-info{
    height: 152px;
    padding: 1.6rem;
    .name{
      text-align: center;
      font-size: 13px;
      color: white;
      margin-top: 0.5rem;
      margin-bottom: 0.5rem;
      padding-left: 1rem;
      padding-right: 1rem;
    }

    .description{
      text-align: center;
      color: rgb(124, 134, 143);
      padding-left: 1rem;
      padding-right: 1rem;
      font-size: 13px;
      margin: 0px;
    }
  }
}
